@import "../../assets/Styles/index.scss";

.container-filter {
  display: flex;
  justify-content: center;
  align-items: center;
}

.icon-filter {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 40px;
  cursor: pointer;
}

.container-btn-load-more {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
  margin-top: 20px;
}

.btn-load-more {
  border: none;
  padding: 15px 40px;
  font-family: inherit;
  background-color: $color-primary;
  color: #fff;
  border-radius: 10px;
  cursor: pointer;
}

.btn-load-more:hover {
  background-color: $color-primary-hover;
  color: #000;
}
